import Vue from 'vue'
import Vonic from 'vonic'

// Page Components
import Index from './components/Index.vue'
import About from './components/About.vue'
import Search from './components/Search.vue'
// Routes
const routes = {
  '/': {
    component: Index
  },

  '/about': {
    component: About
  },

  '/search':{
    component:Search
  }

};

Vue.use(Vonic.app, {routes: routes, defaultRouteUrl: '/'});




